	module.exports = {"data":{"markdownRemark":{"html":"<h2>Brand Identity</h2>\n<p>A company’s brand identity is how that business wants to be perceived by consumers. The components of the brand (name, logo, tone, tagline, typeface) are created by the business to reflect the value the company is trying to bring to the market and to appeal to its customers. Brand identity is separate from brand image – the term for how consumers actually perceive the brand.\nYou can learn more about building a brand and it’s identity on YouTube channels like thefutur</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://www.youtube.com/user/TheSkoolRocks\">the futur YouTube channel</a></li>\n</ul>","fields":{"slug":"/product-design/brand-identity/"},"frontmatter":{"title":"Brand Identity"}}},"pathContext":{"slug":"/product-design/brand-identity/"}}

